<template>
	<div class="main-goods-container">
		<div class="main-filter-bar fixed-top-44" ref="filterBar">
			<filter-bar @active-list="activeList" @handle-filter="handleFilter" :filterIndex="filterIndex" :is-in-stock="isInStock" :sort-order="sortOrder" :is-filtered="isFiltered"></filter-bar>
		</div>

		<div class="add-padding-top-88">
			<goods-list :list="list"></goods-list>
	        <infinite-loading :distance="100" ref=infiniteLoading @infinite="loadList">
	        	<template v-slot:spinner>
	        		<span v-if="isError" class=failed @click="reload">加载失败，点击重试</span>
	        		<div v-else class=loading-gif>
	        			<div class="loader-container">
	        				<div class="loader-wrapper">
	        					<div class="loader-wrapper-inner">
	        						<div></div>
	        						<div></div>
	        					</div>
	        				</div>

	        			</div>
	        		</div>
	        	</template>

	        	<template v-slot:no-more>
	        		<span v-if="isEnd&&shouldShowEnd" class=no-more>已经到底啦_(:з”∠)</span>
	        	</template>
	        	<template v-slot:no-results ref=noResults>
	        		{{errorText}}
	        	</template>
	        </infinite-loading>
		</div>
		<filter-board :visible.sync="isFilterBoardVisible" :categoryList="filterList" :listType="listType" :listTypeId="listTypeId" :isFiltered.sync="isFiltered" @confirm="confirm" ref="filterBoard"></filter-board>
	</div>
  
</template>
<script>
import InfiniteLoading from 'vue-infinite-loading';
import goodsList from '../../components/GoodsList/GoodsList';
import filterBar from '../../components/FilterBar/FilterBar';
import filterBoard from '../../components/FilterBoard/FilterBoard';

import api from '../../models/api'
import vm from '../../globalVM'
import * as utils from '../../utils/utils'
const pageType = {
	"1_1": 3,
	"1_107": 4,
	"1_108": 5,
	"1_109": 6
}
export default {
	props: {
		visible: {
			default: true,
			type: Boolean
		},
		listType: {
			type: String,
			default: "keyword"
		},
		listTypeId: {
			type: String,
			default: ""
		}
	},
	data() {
		return {
			isError: false, 
			pageIndex:1,
			pageSize: 10,
			list: [],  //商品列表
			isMount: true, // loading
			shouldShowEnd: true, //首次加载时只有两个数据时设为false,不显示end isEnd && 
			isEnd: false, //是否还有商品
			errorText: "没有结果，换个关键词试试～",
			scrollTop: "", //滚动时记录当前的位置
			filterIndex: 0, //筛选栏前3个的选中值
			searchForm: {
				keyword: utils.getQueryWord("keyword") ? utils.getQueryWord("keyword") : "",
				ip: utils.getQueryString("ip") || "",
				brand: utils.getQueryString("brand") || "",
				category: utils.getQueryString("category") || "",
				attr: utils.getQueryString("attr") || "",
			},
			state: null, //InfiniteLoading的实例
			pageType: 1,  
			isFirst: true, //加载数据时的第一次的判断
			isInStock: false, //筛选栏第4个的选中值
			sortType: "totalrank", //请求提交的参数, 搜索查询为totalrank, 类别查询为recommend
			sortOrder: false, //价格排序
			isFiltered: false, //筛选栏第5个的选中值
			isFilterBoardVisible: false, // 是否展开筛选面板
			filterList: [], //筛选面板列表数据
			detailFilter: { //筛选面板选中的数据
				categories: {},
				categoriesName: {},
				price: {
					priceCeil:"",
					priceFlow: ""
				}
			},
			isRefresh: true,

		}
	},
	watch: {
		visible(t) {
			// 监听搜索面板打开该组件隐藏
			if(t && this.isRefresh) {
				this.pageIndex = 1;
				this.isEnd = false
				this.list = []
			} else {
				document.documentElement.scrollTop = this.scrollTop
				document.body.scrollTop = this.scrollTop;
			}
		},
		isFilterBoardVisible(t) {
			// 监听筛选面板；当关闭时替换url
			if(!t) {
				this.detailFilter = this.$refs.filterBoard.getFilterList();
				utils.replaceQueryParam("detailFilter", encodeURIComponent(JSON.stringify(this.detailFilter)))
				0 !== Object.keys(this.detailFilter.categories).length  || "" !== this.detailFilter.price.priceCeil && !isNaN(this.detailFilter.price.priceCeil) || "" !== this.detailFilter.price.priceFlow && !isNaN(this.detailFilter.price.priceFlow) || (this.isFiltered = false)
				
			}	
		}
	},
	components: {
		goodsList,
		InfiniteLoading,
		filterBar,
		filterBoard
	},

	created() {
		vm.$on("search-request", this.search)
		vm.$on("search-cancel", this.shouldRefresh)
	},

	beforeDestroy() {
        vm.$off("search-request", this.search);
        vm.$off("search-cancel", this.shouldRefresh);
    },


	mounted() {
		//this.getFromType();
        window.addEventListener("scroll", this.onScoll)
		//this.filterBarOffsetTop = this.$refs.filterBar.offsetTop - 20;
		//console.log(this.filterBarOffsetTop)
	},
	
	methods: {
		search() {

		},
		shouldRefresh() {
			this.isRefresh = false
		},
		backToInit() {
			document.body.scrollTop = 0;
            document.documentElement.scrollTop = 0;
            this.isMount = true;
            this.pageIndex = 1;
            this.list = [];
            this.isFirst = true;
            this.$nextTick(()=>{
            	this.state.reset();
            	//this.$refs.filterBar.style.top = "44px"
            	this.$refs.filterBar.style.top = ""
            })
            vm.$emit("reload")
		},
		handleFilter() {
			this.isFiltered = true
			this.isFilterBoardVisible = true
		},
		confirm() {
			this.backToInit()
		},
		activeList(index, sortType) {
			if(this.canActive) {
				if(3 === index) {
					this.isInStock = !this.isInStock;
					utils.replaceQueryParam("isInStock", this.isInStock);
					this.backToInit()
					return void 0
				}
				this.filterIndex = index;
				this.sortType = sortType;
				0 === index && "keyword" !== this.listType && (this.sortType = "recommend")
				//this.sortOrder = 2 === index && !this.sortOrder
				if(2 === index) {
					this.sortOrder = !this.sortOrder
				} else {
					this.sortOrder = false
				}
				utils.replaceQueryParam("sortType", this.sortType)
				utils.replaceQueryParam("sortOrder", this.sortOrder)
				this.backToInit()
			}
		},
		getFromType() {
			if("" === this.searchForm.keyword) {
				this.fromType = utils.getQueryString("from")
				if(this.searchForm.ip) {
					this.searchForm.ip = "ip"
				} else if(this.searchForm.brand) {
					this.searchForm.brand = "brand"
				}
			} else {
				this.searchForm.brand = "search"
			}
			if(this.searchForm.keyword) {
				this.pageType = 1
			} else if(this.searchForm.category){
				this.pageType = pageType[this.searchForm.category] || 7
			}
			
		},
		onScoll() {
			const filterBar = this.$refs.filterBar
			const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
			const t = this.scrollTop - scrollTop;
			if(scrollTop > 44){
				 //filterBar.style.top = t >= 0 ? "44px" : "0px";
				 filterBar.style.top = t >= 0 ? "" : "0px";
			}
			else {
				  //"0px" !== filterBar.style.top ||  (filterBar.style.top = "44px")
				  "0px" !== filterBar.style.top ||  (filterBar.style.top = "")
			}
			this.visible && (this.scrollTop = scrollTop)

		},
		loadList(node) {
			this.state = node;
			this.canActive = false;
			const detailFilter = utils.getQueryString("detailFilter");
			const params = {
				keyword: utils.getQueryWord("keyowrd") ? utils.getQueryWord("keyowrd"): "",
				filters: detailFilter ? JSON.parse(decodeURIComponent(detailFilter)).categories : "",
				priceFlow: detailFilter ? JSON.parse(decodeURIComponent(detailFilter)).price.priceFlow : "",
				priceCeil: detailFilter ? JSON.parse(decodeURIComponent(detailFilter)).price.priceCeil : "",
				sortType: utils.getQueryString("sortType") || ("keyword" === this.listType ? "totalrank" : "recommend"),
				sortOrder: "price" === utils.getQueryString("sortType") ? "price" === utils.getQueryString("sortType") ? "asc": "desc" :"",
				pageIndex: this.pageIndex ,
				mid: "",
				state: "true" === utils.getQueryString("isInStock") ? 0 : "",
			}
			if("keyword" !== this.listType){
				params.type = this.listType
				params.id = this.listTypeId 
				this.getListByCategory(node, params)
			} else {
				this.getListByCategory(node, params)
			}
			
		},
		reload(){
			this.isError = false;
			this.loadList();
		},
		handleError() {
			this.isNoResult = true
			const detailFilter = utils.getQueryString("detailFilter");
			const obj = detailFilter && JSON.parse(decodeURIComponent(detailFilter))
			// 没有筛选参数或者筛选参数没选--没有相关数据
			if(!obj || 0 === Object.keys(obj.categories).length && ""=== obj.price.priceCeil && ""=== obj.price.priceCeil) {
				if(""===this.searchForm.keyword&&""===this.searchForm.brand&&""===this.searchForm.ip&&""===this.searchForm.category&&"" === this.searchForm.attr) {
					this.errorText = "页面不存在"
				} else if("" === this.searchForm.keyword) {
					this.errorText = "分类下没有商品"
				} else if("" === this.isInStock) {
					this.errorText = "没有现货"
				} else {
					this.errorText = "没有结果，换个关键词试试～"
				}
			} else  {
				this.errorText = "没有结果，减少筛选试试～"
			}
		},
		getListByCategory(node, params) {
			if(this.isModel) {
				params.feedType=utils.getQueryString("feedType");
				params.homePageId=utils.getQueryString("homePageId");
				params.pageInfoId=utils.getQueryString("pageInfoId");
				params.seriesId=utils.getQueryString("seriesId");
			}
			api.getGoods({
				params
			}).then(({status, msg, data})=> {
				vm.isLoading = false;
				this.canActive = true
				this.isError = false;
				this.pageIndex++;
				this.isMount && (this.$emit("return-data", data), this.isMount = false); //回调加载页面信息
				// 当数据为空
				if(!data || !data.list || !data.list.length) {
					// 判断是否第一次加载是数据为空, 则加载推荐列表
					// this.isFirst && ()
					this.handleError();
					this.isEnd = true;
					return void node.complete() 
				}
				this.shouldShowEnd = true;
				if(1 === data.pageIndex) {
					this.filterList = data.searchFilter //筛选面板数据
					data.list && 2 >= data.list.length && (this.shouldShowEnd = false)
				}
				this.isFirst = false;
				this.list = [...this.list, ...data.list]
				node.loaded();



			}).catch(()=>{
				vm.isLoading = false;
				this.canActive = true;
				this.isError = false;
				this.$emit("return-data", null)
				this.isMount && (this.$emit("return-data", null), this.isMount = false);
				if(1 === this.pageIndex) {
					this.errorText = "开小差"
					node.complete()
				} else {
					this.isError = true
				}
			})
		}
	}
}
</script>
<style lang=scss>
.main-filter-bar {
	width: 100%;
	box-shadow: 0 2px 6px rgba(0,0,0,.1);
}
.loading-gif {
	width: 28px!important;
    height: 28px!important;
	margin: 15px 0 8px!important;
	.loader-container{
		width: 100%;
		height: 100%;
		position: relative;
		.loader-wrapper {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 28px;
			height: 28px;
			.loader-wrapper-inner {
				position: relative;
				> div {
					border: 1px #000 solid;
					border-bottom-color: transparent;
			    	border-top-color: transparent;
			    	border-radius: 100%;
			    	width: 28px;
			    	height: 28px;
			    	position: absolute;
			    	left: -14px;
			    	top: -14px;
			    	animation: rotate 1s linear infinite;
				}
				>div:last-child {
					width: 10px;
			    	height: 10px;
			    	left: -5px;
			    	top: -5px;
			    	display: inline-block;
			    	animation: rotate2 .5s ease-in-out infinite reverse
				}
			}
		}
		
	}
}
@keyframes rotate{
	0% {
		transform: rotate(0deg) scale(1);
	}
	50% {
		transform: rotate(180deg) scale(1);
	}
	100% {
		transform: rotate(360deg) scale(1);
	}
}
@keyframes rotate2{
	0% {
		transform: rotate(0deg) scale(1);
	}
	100% {
		transform: rotate(360deg) scale(1);
	}
}
</style>


