<template>
	<popup :visible="visible" @close="onClose" >
		<div class="unvalid-list">
			<div class="unvalid-list-container">
				<div class="item" v-for="(item, index) in unvalidlist">
					<div class="item-img" :class="{'item-failed-img': item.showFailed}">
						<img :src="item.itemsThumbImg" v-if="!item.showFailed">
					</div>
					<div class="item-info">
						<div class="item-info-container" :class="{line: index+1 !== unvalidlist.length}">
							<div class="name word-ellipsis-2">{{item.itemsName}}</div>
							<div class="spec">{{item.skuSpec}}</div>
						</div>
						
					</div>
				</div>

			</div>
		</div>
		<div class="cart-info">{{warningInfo}}</div>
		<div class="button" @click="onClose">
			<span>返回修改</span>
		</div>
	</popup>
</template>

<script>
import popup from "../../components/Popup/Popup"
export default {
	props: {
		visible: {
			default: false,
			type: Boolean
		},
		warningInfo: {
			default: "",
			type: String
		},
		unvalidlist: {
			default: ()=>[],
			type: Array
		},
	},
	components: {
		popup
	},
	methods: {
		onClose() {
			this.$emit("close")
		},
	}
}
</script>
<style lang="scss">
.unvalid-list {
	padding-top: 30px;
	.unvalid-list-container {
		max-height: 367px;
    	overflow-y: scroll;
		.item {
	        display: flex;
	        flex-direction: row;
	        position: relative;
	        height: 90px;
	        overflow: hidden;
	        .item-img {
	            width: 80px;
	            height: 80px;
	            line-height: 80px;
	            margin: 5px 0 0 15px;
	            position: relative;
	            background-color: #f4f5f7;
	            img {
	                width: 100%;
	                height: 100%;
	                display: block;
	                
	            }
	            &:after {
                	content: "";
				    position: absolute;
				    top: 0;
				    bottom: 0;
				    right: 0;
				    left: 0;
				    background: hsla(0,0%,100%,.7);
                };
	            &.item-bck-img {
	                background-image: url(../../resoures/images/loading-img.png);
	                background-repeat: no-repeat;
	                background-size: 40px 40px;
	                background-color: #f4f5f7;
	                background-position: 50%;
	            }
	            &.item-failed-img{
	                background-image: url(../../resoures/images/failed-img.svg);
	                background-repeat: no-repeat;
	                background-size: 40px 40px;
	                background-color: #f4f5f7;
	                background-position: 50%;
	            }
	        }
	        .item-info {
	            flex: 1;
	            padding-top: 7px;
	            position: relative;
	            height: 100%;
	            .item-info-container {
	                margin-left: 12px;
	                margin-right: 22px;
	                height: 100%;
	                position: relative;
	                overflow: hidden;
	                &.line {
	                	box-shadow: inset 0 -1px 0 0 #e7e7e7;
	                }
	                .name {
	                    font-size: 14px;
	                    line-height: 18px;
	                    overflow: hidden;
	                    color: #aaa; 
	                }

	                .spec {
	                    position: absolute;
	                    bottom: 7px;
	                    left: 0;
	                    font-size: 12px;
	                    color: #aaa; 
	                }

	            }

	        }
	    }
	}
}
.cart-info {
	padding: 16px 0;
	font-size: 14px;
	text-align: center;
	line-height: 17px;
	box-shadow: 0 -1px 2px 0 rgba(0,0,0,.08);
}
.button {
	margin: 0 10px 10px;
    border: 1px solid #4cc3ff;
    border-radius: 36px;
    font-size: 14px;
    text-align: center;
    line-height: 44px;
    color: #4cc3ff;
    span {
    	color: #4cc3ff
    }
}

</style>


