import {get, post} from '@/utils/http';

export default {
  /**
   * 获取用户信息
   * @param options
   * @returns {*}
   */
  getFeed: function(options = {}) {
    return get('api/home/index', options);
  },
  getList: function(options = {}) {
    return get('api/home/category/list', options);
  },
  getGoods: function(options = {}) {
    return get('api/search/category', options);
    //return get('/api/mall-c-search/search/category', options);
  },
  getCartList: function(options = {}) {
    return post('api/cart/na/index', options);
  },
  getUserInfo: function(options = {}) {
    return get('web-interface/nav', options);
  },
  getSkus: function(options = {}) {
    return get('api/items/sku/list', options);
  },
  getCount(options = {}) {
    return get('api/ticket/ordercenter/count', options)
  },
  getOrderList(options = {}) {
    return get('api/ticket/ordercenter/list', options)
  }
}



