<template>
	<div class="dialog">
		<div class="dialog-container">
			<div class="dialog-img"></div>
			<div class="dialog-info">是否从购物车中移除该商品？</div>
			<div class="dialog-action">
				<div class="dialog-cancel" @click="cancel">CANCEL</div>
				<div class="dialog-ok" @click="ok">OK</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	methods: {
		cancel(){
			this.$emit("cancel-click")
		},
		ok() {
			this.$emit("confirm-click")
		},
	}

}
</script>
<style lang="scss">
.dialog{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 100002;
    background: rgba(0, 0, 0, 0.8);
    .dialog-container{
    	position: absolute;
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    max-width: 6.9rem;
	    height: 4.5rem;
	    border-radius: 0.24rem;
	    background-color: #fff;
	    z-index: 99;
	    margin: auto;
	    .dialog-img {
			
	    }
	    .dialog-info {
	    	padding-top: 110px;
	    	padding-bottom: 20px;
    		font-size: 18px;
    		line-height: 18px;
    		text-align: center;
	    }
	    .dialog-action {
    	    padding-left: 20px;
		    padding-right: 20px;
		    display: flex;
		    .dialog-cancel {
				height: 40px;
				line-height: 40px;
				max-width: 150px;
				flex-grow: 1;
				width: 0;
				color: #9d9d9d;
				text-align: center;
				border: 1px solid #9d9d9d;
		    }
		    .dialog-ok{
				height: 40px;
				line-height: 40px;
				max-width: 150px;
				flex-grow: 1;
				width: 0;
				text-align: center;
				margin-left: 16px;
				color: #4cc3ff;
				border: 1px solid #4cc3ff;
		    }
	    }

    }
}
</style>


