/**
 * 加强控制函数
 * @param  {gen} gen Generator 生成函数function* ()
 * @return {Funtion}     返回一个函数，调用时无需传参
 */
export default function(gen) {
	return function() {
		gen = gen.apply(this, arguments);
		return new Promise(function(resolve, reject) {
			return function onFulfilled(type, res) {
				try {
					var ret = gen[type](res),
						value = ret.value
				} catch (e) {
					return void reject(e)
				}
				if (!ret.done) return Promise.resolve(value)
					.then(function(res) {
						onFulfilled("next", res)
					}, function(res) {
						onFulfilled("throw", res)
					});
				resolve(value)
			}("next")
		})
	}
}


