<template>
	<div class="popup" @click.stop @touchmove.stop.prevent>
		<transition name="fade-popup">
			<div v-show="visible" class="popup-mask" @click.stop="onClose" @touchmove.stop.prevent></div>
		</transition>
		<transition name="fade-popup">
			<div v-show="visible" class="popup-container">
				<div class="popup-close" @click.stop="onClose"><i class="anticon icon-close"></i></div>
				<div class="popup-body">
					<slot></slot>
				</div>
			</div>
		</transition>
	</div>
</template>
<script>
export default {
	props: ["visible"],
	methods: {
		onClose() {
			this.$emit("close")
		}
	}
}
</script>
<style lang="scss">
.popup{


	.popup-mask {
		position: fixed;
	    top: 0;
	    bottom: 0;
	    left: 0;
	    right: 0;
	    background: #000;
	    opacity: .5;
	    z-index: 10002;
	}
	.popup-container {
		position: fixed;
	    bottom: 12px;
	    left: 12px;
	    right: 12px;
	    background: #FFF;
	    z-index: 10003;
	    border-radius: 4px;

	    .popup-close {
	    	width: 40px;
	    	height: 40px;
	    	position: absolute;
	    	top: -20px;
	    	right: 12px;
	    	background: #f5f5f5;
	    	border-radius: 40px;
	    	box-shadow: 0 2px 4px 0 rgba(0,0,0,.08);
	    	z-index: 10004;
	
	    }
	    .popup-body {
	    	width: 100%;
	    	height: 100%;
	    	position: relative;
	    	overflow: auto;
	    }
	}
	.fade-popup-enter-active,.fade-popup-leave-active{
	    transition:opacity .2s
	}
	.fade-popup-enter,.fade-popup-leave-to{
	    opacity:0
	}
}


</style>


