<template>
	<div class="filter-bar-container">
		<ul>
			<li :class="{active: 0 === filterIndex}" @click="()=>{return activeList(0, 'totalrank')}">综合</li>
			<li :class="{active: 1 === filterIndex}" @click="()=>{return activeList(1, 'sale')}">销量</li>
			<li :class="{active: 2 === filterIndex}" @click="()=>{return activeList(2, 'price')}">
				<div class="price">
					<span>价格</span>
					<div class="icon-container">
						<i class="anticon icon-AscendFilled" :class="{active: 2 === filterIndex && sortOrder}"></i>
						<i class="anticon icon-DescendFilled" :class="{active: 2 === filterIndex && !sortOrder}"></i>
					</div>
				</div>
			</li>
			<li :class="{active: isInStock}" @click="()=>{return activeList(3)}">
				<div class="in-stock">
					<div class="price-area" @click="(e)=>{return e.stopPropagation(), activeList(2)}"></div>
					<div class="sep"></div>
					<div class="word">现货</div>
				</div>
			</li>
			<li :class="{active: isFiltered}" @click="handleFilter">筛选<i class="anticon icon-filter"></i></li>
		</ul>
	</div>
</template>
<script>
export default {
	props: ["filterIndex", "isInStock", "sortOrder", "isFiltered"],
	data() {
		return {

		}
	},
	mounted() {
	},
	methods: {
		activeList(...args) {
			this.$emit("active-list", ...args)
		},
		handleFilter() {
			this.$emit("handle-filter")
		}
	}
}
</script>
<style lang=scss>
.jiageqiehuan {
    width: 1em;
    height: 1em;
    vertical-align: -.15em;
    fill: currentColor;
    overflow: hidden;
}
.filter-bar-container {
	ul {
		width: 100%;
		display: flex;
		height: 44px;
		position: relative;
		background-color: #FFF;
		li {
			flex: 1;
			height: 44px;
			line-height: 44px;
			font-size: 14px;
			text-align: center;
			&.active {
				color: #4cc3ff;
			}
			.icon-filter{
				font-size: 12px;
				margin-left: 2px;
			}
		}
		.price {
			position: relative;
			.icon-container {
				    position: absolute;
				    top: 0;
				    left: 50%;
				    width: 24px;
				    height: 44px;
				    margin-left: 15px;
				    line-height: 0;
				    vertical-align: middle;
				    .icon-AscendFilled {
						font-size: 10px;
					    padding: 18px 6px 4px;
					    display: inline-block;
					    color: #212121;
					    &.active {
					    	color: #4cc3ff;
					    }

				    }
				    .icon-DescendFilled {
						font-size: 10px;
					    display: inline-block;
					    padding: 4px 6px 18px;
					    color: #212121;
					    &.active {
					    	color: #4cc3ff;
					    }
				    }
			}
			.jiageqiehuan {
				font-size: 14px;
    			vertical-align: middle;
			}
		}
		.in-stock {
			position: relative;
			.price-area {
				width: 15px;
				height: 44px;
			}
			.sep {
				position: absolute;
				top: 10px;
				height: 24px;
				left: 15px;
				border-right: 1px solid #d7d7d7
			}
			.word {
				position: absolute;
			    top: 0;
			    right: 10px;
			}
		}
	}
}
</style>


