import Vue from 'vue'
import error from '../../components/Error/Error'
import loading from '../../components/Loading/Loading'
import flexible from '../../utils/simple-flexible'
import navBar from '../../components/NavBar/NavBar'
import imageLoad from '../../components/ImageLoad/ImageLoad'
import searchBoard from '../../components/SearchBoard/SearchBoard'
import mainGoodsList from '../../components/MainGoodsList/MainGoodsList'
import cartConfirm from '../../components/CartConfirm/CartConfirm'
import cartToast from '../../components/CartToast/CartToast'
import cartPopup from '../../components/CartPopup/CartPopup'
import  '../../resoures/css/animate.css'

import co from '../../utils/co'
import userInterFace from '../../utils/user-interface';

import  '../../resoures/css/main.scss'
import  '../../resoures/css/common.scss'
import  '../../resoures/font/iconfont.css'

import './cart.scss'
import api from '../../models/api'
import * as utils from '../../utils/utils'

//import fastclick from 'fastclick';
import 'babel-polyfill';
//import 'lodash'
flexible()


// FOR H5 处理移动端click 延时 300ms的问题
// fastclick.attach(document.body);

// 根据当前环境选择性加载 mock 包，发布的时候也不用注释和删除
//if(process.env.NODE_ENV === 'development') {
  require('./mock');
//}



new Vue({
  el: '#app',
  data() {
    return {
      params: {},
      shopList: [],
      shopName: "",
      showPageRefresh: false,  // reload标识
      localStorageSkuList: [],  // 未登录本地购物车列表
      isLoading: false,
      isError: false,
      warehouseList: [],
      warehouseObj: {},
      checkedNum: 0,
      allShop: false,
      unvalidList: [],
      itemsNum: 0,
      totalMoney:0,
      totalTax: 0,
      selectedItemIdList: [],
      selectedItemList: [],
      skuIdString: "", //本地记录向购物车添加的商品 "[{shopid: , skuid:}]"
      skuIdList: [], //skuIdString 的数据转换
      overseaItemNum: 0, // 海外商品数量
      totalTax: 0, 
      allShop: false, // 全选标识
      unvalidSku: [],  // 存储不正确的商品
      showUnvalidPopup: false,
      closeAndRefresh: true, //是否从新获取数据
      popupWarningInfo: "",
      deleteShop: {}, //点击减少数量时记录记录点击的商品
      deleteItemIndex: 0,
      deleteShopIndex: 0,
      ifLogin: false,
      showDelete: false,
      defaultCheck: false,
      showImg: true,
      refreshDown: true,
      refreshInfo: "再拉，再拉就刷新给你看",
      refreshInfoList: ["再拉，再拉就刷新给你看", "够了啦，松开人家嘛", "刷啊刷啊，好累啊，喵"],
      toastInfo: "",
      showToast: false


    }

  },
  created() {
    //是否购物车从提交订单 =》 登录 =》 购物车
    if(localStorage.fromLogin) {
      //...
      //checkOrder()
      this.getInit()
    } else {
      this.getInit()
    }
    
  },
  components: {
  	navBar,
  	searchBoard,
  	imageLoad,
  	error,
  	loading,
    mainGoodsList,
    cartConfirm,
    cartToast,
    cartPopup
  },

  methods: {
    closePopup() {
      this.showUnvalidPopup = false;
      this.closeAndRefresh && this.getInitInfo()
    },
    toast(info) {
      this.showToast = true;
      this.toastInfo = info || ""
      setTimeout(this.hideToast, 1000)
    },
    hideToast() {
      this.showToast = false
    },

    checkDefaultItem() {

      const skuIdList = this.skuIdList;
      const skuIdString = this.skuIdString;
      if( skuIdList && skuIdList.length) {

        const changeItemCheckStateInShop = (index) => {
          const shop = this.shopList[index]
          skuIdList.forEach(c=>{
            const item = shop.itemsList.filter(item=>parseInt(item.skuId) === parseInt(c.skuId))

            item.length && this.changeItemCheckState(shop, item[0])
          })
        }
        for(let i =0; i < this.shopList.length; i++) {
          changeItemCheckStateInShop(i)
        }
      }
      // 重详情页跳过了会有选择状态，执行后选中状态清空，第二次进入不执行
      localStorage.skuIdList = JSON.stringify([]);
      this.defaultCheck = false;
      this.skuIdString = "";
      this.skuIdList = []
    },
    cancelDelete() {
      this.showDelete = false;
    },
    confirmDelete() {
      this.showDelete = false;
      const shop = this.deleteShop;
      const item = this.deleteShop.itemsList[this.deleteitemIndex];
      if(this.ifLogin) {
        this.toast("update serve")
      } else{
        this.toast("update localStorage")
      }
      shop.itemsList.splice(this.deleteitemIndex, 1)
      0 === shop.itemsList.length && this.shopList.splice(this.deleteShopIndex, 1)
      shop.itemsTotalMoney = Math.floor((shop.itemsTotalMoney - (item.cartIsCheck ?  item.price : 0)) * 100) /100
      this.totalMoney = Math.floor((this.totalMoney - (item.cartIsCheck ?  item.price : 0)) * 100) /100;
      item.taxPrice && ( this.totalTax = Math.floor((this.totalTax - (item.cartIsCheck ?  item.taxPrice : 0)) * 100) /100)
      item.itemsIsOversea && item.cartIsCheck && this.overseaItemNum--;
      this.itemsNum--;
      item.cartIsCheck && this.checkedNum--;
      item.cartIsCheck && this.changeCheckedItemList(item, true, false)
      item.cartIsCheck && this.warehouseObj[item.warehouseName].checkedNum--;
      this.warehouseObj[item.warehouseName].num--;
      this.warehouseList = this.warehouseList.filter(c=>!!c.num)
      0 !== shop.itemsList.length && this.checkShop(shop, true);
      const shopIsCheckAll = 0 === shop.itemsList.length || shop.checkAll;
      this.checkAllShop(shopIsCheckAll);

    },
    getInit(){
      api.getUserInfo().then(({status, data, msg})=>{
        console.log(status,data)
        this.mid = data.mid;
        this.ifLogin = true;
        console.log("已登录")
        this.getInitInfo()
      }).catch(()=>{
        console.log("未登录")
        this.getInitInfo()
      })
    },
    getFloat(t) {
      const decimals = Math.floor(100 * t) % 100;
      return decimals < 10 ? "0" + decimals : decimals
    },
    addSkuNum(shop, item) {
      if(item.skuNum + 1 > 99) {
        this.toast("超过99")
      } else if(item.storage < item.skuNum + 1){
        this.toast("超过库存")
      } else if(item.spuLimitNum >= 0 && this.judgeSpuLimitation(shop, item, 1)) {
        this.toast("限制")
      } else{
        item.skuNum++;
        let flag = false;
        this.warehouseList.forEach(c=> {
          c.warehouse !== item.warehouseName && !!c.checkedNum && (flag = true)
        })
        if(!flag) {
          if(item.cartIsCheck){
            this.changeCheckedItemList(item, false, true)
            item.itemsTotalMoney = Math.floor((item.itemsTotalMoney + item.price) * 100) /100;
            this.totalMoney = Math.floor((this.totalMoney + item.price) * 100) /100;
            if(item.taxPrice) {
              this.totalTax = Math.floor((this.totalTax + item.taxPrice) * 100) /100;
            }
            this.checkedNum++
          } else {
            this.changeItemCheckState(shop, item)
          }
        }
        if(this.ifLogin) {
          this.toast("update serve")
        } else{
          this.toast("update localStorage")
        }
      }
    },
    reduceSkuNum(shop, index, shopIndex) {
      const item = shop.itemsList[index];
      let flag = false;
      this.warehouseList.forEach(c=> {
        // if(c.warehouse !== item.warehouseName) {
        //   flag = !!c.checkedNum || flag
        // }
        c.warehouse !== item.warehouseName && !!c.checkedNum && (flag = true)
      })
      this.deleteShop = shop;
      this.deleteShopIndex = shopIndex;
      this.deleteitemIndex = index;
      if(1 === item.skuNum) 
        this.showDelete = true
      else {
        item.skuNum--
        if(!flag) {
          if(item.cartIsCheck){
            this.changeCheckedItemList(item, false, true)
            item.itemsTotalMoney = Math.floor((item.itemsTotalMoney - item.price) * 100) /100;
            this.totalMoney = Math.floor((this.totalMoney - item.price) * 100) /100;
            if(item.taxPrice) {
              this.totalTax = Math.floor((this.totalTax - item.taxPrice) * 100) /100;
            }
            this.checkedNum--
          } else {
            this.changeItemCheckState(shop, item)
          }

        }
        if(this.ifLogin) {

          this.toast("update serve")
        } else{
          this.toast("update localStorage")
        }
      }
    },
    imgLoaded(item) {
      item.showBck = false
    },
    imgFailed(item) {
      item.showBck = false
      item.showFailed = true
    },
    goToDetail(item) {
      window.location.href = location.origin + "/detail.html?itemsId=" + item.itemsId
    },
    changeAllShopCheckState() {
      if(this.shopList.length)
        if(1 === this.warehouseList.length) {
          this.shopList.forEach(shop=> {

            let allUnvalidItem = [];
            // this.allShop = true 的必然条件是所有shop.checkAll = ture;
            // 当两者都为true时，为取消全选
            if(shop.checkAll == this.allShop) {
              const unvalidItem = this.changeShopCheckState(shop, false);
              Object.keys(unvalidItem).forEach(c=>{
                allUnvalidItem = [...allUnvalidItem, ...unvalidItem[c]]
              })
            }

            if(allUnvalidItem.length) {
              this.unvalidSku = allUnvalidItem;
              this.showUnvalidPopup = true;
              // 关闭但不刷新
              this.closeAndRefresh = false;
              this.popupWarningInfo = "以上" + allUnvalidItem.length + "件商品超过购买数量，请重新选择"
            } else {
              this.allShop = !this.allShop;
            }
          })
        } else {
          this.toast("多仓选择了")
        }
    },
    /**
     * [changeShopCheckState description]
     * @param  {[type]}  shop           [description]
     * @param  {Boolean} isCheckAllShop 是否允许多仓库
     * @return {[type]}                 [description]
     */
    changeShopCheckState(shop, isCheckAllShop) {
      let totalMoney = 0;
      let totalTax = 0;
      let checkedNum = 0;
      let unvalidItem = shop.checkAll ? {} : this.checkShopItemLimitation(shop);
      shop.itemsList.forEach(item => {
        // 不正确的或者状态不一致的不执行
        if(item.cartIsCheck == shop.checkAll && !unvalidItem[item.itemsId]){
          const sum = Math.floor(item.price * item.skuNum * 100) / 100;

          item.cartIsCheck = shop.checkAll ? 0 : 1;
          if(shop.checkAll) {
            //item.itemsTotalMoney = Math.floor(item.itemsTotalMoney - sum) / 100;
            totalMoney = Math.floor((totalMoney - sum) * 100) / 100; //价格
            checkedNum = checkedNum - item.skuNum;
          } else {
            //item.itemsTotalMoney = Math.floor(item.itemsTotalMoney + sum) / 100;
            totalMoney = Math.floor((totalMoney + sum) * 100) / 100;
            checkedNum = checkedNum + item.skuNum
          }
          if(item.taxPrice) 
            if(shop.checkAll) {
              totalTax = Math.floor((totalTax - item.taxPrice * item.skuNum)* 100) / 100;// 税费
            } else {
              totalTax = Math.floor((totalTax + item.taxPrice * item.skuNum)* 100) / 100;
            }

          if(item.itemsIsOversea)
            if(shop.checkAll) {
              this.overseaItemNum--
            } else {
              this.overseaItemNum++
            }
          this.changeCheckedItemList(item, 0 === item.cartIsCheck, false)
        }
        
      }) 




      "{}" !== JSON.stringify(unvalidItem) || (shop.checkAll = !shop.checkAll)
      isCheckAllShop && this.checkAllShop(shop.checkAll)
      shop.itemsTotalMoney = Math.floor((shop.itemsTotalMoney + totalMoney)* 100) / 100
      this.totalTax = Math.floor((this.totalTax + totalTax)* 100) / 100;
      this.totalMoney = Math.floor((this.totalMoney + totalMoney)* 100) / 100;
      this.checkedNum += checkedNum;
      
      return unvalidItem
    },
    // 检查数量超出库存的商品并返回
    checkShopItemLimitation(shop) {
      const obj = {} //存放商品数量
      const uncheckObj = {} //没有选择的集合
      shop.itemsList.forEach(item=>{
        if(item.spuLimitNum > 0) {
          // 累计相加
          obj[item.itemsId] = obj[item.itemsId] ? obj[item.itemsId] + item.skuNum : item.skuNum;
          // 没有选择的
          if(!item.cartIsCheck) {
            uncheckObj[item.itemsId] || (uncheckObj[item.itemsId] = []);
            uncheckObj[item.itemsId].push(item)
          }
        }
      })
      Object.keys(obj).forEach(c=> {
        // 商品数量没有超过库存的删掉，返回剩下超出库存
        uncheckObj[c] &&  obj[c] <= uncheckObj[c][0].spuLimitNum && delete uncheckObj[c]
      })
      return uncheckObj
    },
    changeItemCheckState(shop, item) {
      let flag = false;
      this.warehouseList.forEach(c=>{
        // 只允许同时选择一个仓库
        // 选a，b仓，若b已经选择 !!c.checkedNum = true， a仓不允许选择，若b仓没选择 !!c.checkedNum = false，a仓允许选择 
        c.warehouse !== item.warehouseName &&  !!c.checkedNum && (flag = true)
      })

      if(!item.cartIsCheck && item.spuLimitNum >=0 && this.judgeCheckedSpuLimitation(shop, item)) {
        this.toast("超过库存")
      } else if(flag) {
        this.toast("多仓选择了")
      } else {
        // 计算价格
        let sum = Math.round(item.price * item.skuNum * 100) / 100
        //更改状态
        item.cartIsCheck = item.cartIsCheck ? 0 : 1
        if(item.cartIsCheck) {
           shop.itemsTotalMoney = Math.round((shop.itemsTotalMoney + sum) * 100) /100
           this.totalMoney = Math.round((this.totalMoney + sum) * 100) /100
           this.checkedNum += item.skuNum;
           this.warehouseObj[item.warehouseName].checkedNum++ ;
        } else{
           shop.itemsTotalMoney = Math.round((shop.itemsTotalMoney - sum) * 100) /100
           this.totalMoney = Math.round((this.totalMoney - sum) * 100) /100
           this.checkedNum -= item.skuNum;
           this.warehouseObj[item.warehouseName].checkedNum-- ;
        }
        if(!!item.taxPrice) {
          let tax = Math.round(item.taxPrice * item.skuNum * 100) / 100;
          if(item.cartIsCheck) {
             this.totalTax = Math.round((this.totalTax + tax) * 100) /100
          } else{
            this.totalTax = Math.round((this.totalTax - tax) * 100) /100
          }
        }
        if(item.itemsIsOversea) {
          if(item.cartIsCheck) {
            this.overseaItemNum++
          } else{
            this.overseaItemNum--
          }
        } 
        this.changeCheckedItemList(item, 0 === item.cartIsCheck, false)
        this.checkShop(shop, item.cartIsCheck);
        this.checkAllShop(shop.checkAll)


        
      }
    },
    checkShop(shop, itemIsCheck) {

      if(itemIsCheck) {
        let flag = true;
        for(let i=0; i< shop.itemsList.length; i++) {
          flag = shop.itemsList[i].cartIsCheck && flag
        }
        shop.checkAll = flag;
      } else 
        shop.checkAll = false;
    },
    checkAllShop(shopIsCheckAll) {
      if(shopIsCheckAll) {
        let flag = true;
        for(let i=0; i< this.shopList.length; i++) {
          flag = this.shopList[i].checkAll && flag
        }
        this.allShop = flag;
      } else 
        this.allShop = false;
    },
    judgeSpuLimitation(shop, item, count) {
      let sum = 0;
      const itemsList = shop.itemsList;
      for(let i=0; i<itemsList.length; i++) {
        itemsList[i].itemsId === item.itemsId && (sum += itemsList[i].skuNum)

      }
      return (sum + count) > item.spuLimitNum
    },
    judgeCheckedSpuLimitation(shop, item) {
      // 库存限制
      // 同一商品不同规格 加起来也不能超过库存
      const itemsList = shop.itemsList;
      let sum = 0;
      for(let i =0; i<itemsList.length; i++) {
        if(item.itemsId === itemsList[i].itemsId) {
          if(itemsList[i].cartIsCheck) sum+=itemsList[i].skuNum
          else sum+=0
        }
      }

      return (sum + item.skuNum) > item.spuLimitNum
    },
    /**
     * [changeCheckedItemList description]
     * @param  {[type]}  item        [description]
     * @param  {[type]}  cartIsUncheck 最后状态是否为【取消选择】
     * @param  {Boolean} isChangeNum 是否是修改数量
     * @return {[type]}              [description]
     */
    changeCheckedItemList(item, cartIsUncheck, isChangeNum) {
      const checkedItem = {};
      const index = this.checkedItemIdList.indexOf(item.skuId)
      if(index >= 0 && cartIsUncheck && !isChangeNum) {
        // 从已选择到取消选
        this.checkedItemIdList.splice(index, 1);
        this.checkedItemList.splice(index, 1)
      } else if( index < 0 && !cartIsUncheck && !isChangeNum) {
        // 从未选择到选择
        checkedItem.skuId = item.skuId;
        checkedItem.itemsId = item.itemsId;
        checkedItem.skuNum = item.skuNum;
        checkedItem.cartId = item.cartId;
        checkedItem.price = item.price;
        checkedItem.shopId = item.shopId;
        checkedItem.saleType = item.saleType;
        checkedItem.itemsIsOversea = item.itemsIsOversea;
        this.checkedItemIdList.push(checkedItem)
        this.checkedItemList.push(checkedItem)
      } else if (index >= 0 && !cartIsUncheck && isChangeNum){
        this.checkedItemList[index].skuNum = item.skuNum
      }
    },
    getInitInfo() {
      // 判断本地的购物车是否有记录，有则向服务器提交该数据
      if(this.localStorageSkuList.length) {
        this.params = {
          items: this.localStorageSkuList
        }
      } else this.params = {}
      this.showPageRefresh && (this.isLoading = true, this.isError = false)
      this.isLoading = true;
      this.isError = false;
      this.warehouseList = [];
      this.warehouseObj = {};

      api.getCartList(this.params).then(({data,status,msg})=>{
        this.isLoading = false;
        this.shopName = data.shopList[0] ? this.cutString(data.shopList[0].shopName) : ""
        this.shopList = data.shopList.map((shops)=>{
          shops.checkAll = false;
          shops.itemsList.forEach(e=> {
            this.$set(e, "showBck", true) //图片loading
            this.$set(e, "showFailed", false)
            this.warehouseObj[e.warehouseName] ? this.warehouseObj[e.warehouseName].num++ :this.warehouseObj[e.warehouseName] = {
              num: 1,
              warehouse: e.warehouseName,
              checkedNum: 0
            }
          })
          Object.keys(this.warehouseObj).forEach(key=> {
            this.warehouseList.push(this.warehouseObj[key])
          })
          return shops
        })
        this.checkedNum = 0;
        this.allShop = false;
        this.unvalidList = data.invalidList.map(e=> {
          this.$set(e, "showBck" , true)
          this.$set(e, "showFailed" , false)
          return e
        })
        this.itemsNum = data.itemsNum
        this.totalMoney = 0
        this.totalTax = 0
        this.checkedItemIdList = []
        this.checkedItemList = []
        // 根据该参数判断是否从明细页面跳向购物车
        const isFromDetail = utils.getQueryString("isFromDetail")

        if(!this.skuIdString) {
          this.skuIdString = localStorage.skuIdList
          if("[]"!==this.skuIdString && this.skuIdString) {
            this.skuIdList = JSON.parse(this.skuIdString)
          } else{
            this.skuIdList = null
          }
        }
        //只有从明细页添加了且由明细页面跳向购物车
        if(this.defaultCheck || isFromDetail) this.checkDefaultItem()
        this.isLoading = false;
        this.showPageRefresh = true;
        // 请求成功后添加下来刷新事件
        this.downRefresh()

        
        
      }).catch(()=>{
        this.isLoading = false;
        this.showPageRefresh &&  (this.isLoading = false, this.isError = true)
        this.showPageRefresh = false;
      })

    },
    downRefresh() {

      let scrollContent = null;
      let startY = 0;
      let endY = 0;
      let isPullDown = false
      let tracking = false;
      let isCanRefresh = false
      this.$nextTick(()=>{
        scrollContent = this.$refs.scrollContent

        if(scrollContent) {
          
          scrollContent.addEventListener("touchstart", touchStart, false)
          scrollContent.addEventListener("touchmove", touchMove, false)
          scrollContent.addEventListener("touchend", touchEnd, false)
        }
        
      })
      // 判断是否位于顶部
      // 记录初始触摸的位置
      // 更改状态为滑动中
      const touchStart = (e) =>{
        const touches = e.targetTouches[0];
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        scrollContent.style.transition = "";

        if(0 === scrollTop) {
          startY = touches.pageY;
          tracking = true
        }
      }
      // 判断状态是否为滑动
      // 记录滑动的距离
      // 判断距离是否满足一定数值 
      // 满足则更改数据，否则复原数据
      const touchMove = (e) =>{
        if(tracking) {
          endY = e.targetTouches[0].pageY;
          // 触摸的距离
          let touchDistance = endY - startY;
          isPullDown = touchDistance > 0
          if(isPullDown){
            e.preventDefault()
            scrollContent.style.transform = `translateY(${touchDistance * .4}px)`
            // DOM移动的距离 
            const moveDistance = touchDistance * 0.4 ;
            isCanRefresh = moveDistance > 96
            //if( touchDistance * 0.4 > 240) 
            if(isCanRefresh) {
              this.refreshInfo = this.refreshInfoList[1];
              this.refreshDown = false;
            } else{
              this.refreshInfo !== this.refreshInfoList[0] && (this.refreshInfo = this.refreshInfoList[0], this.refreshDown = true)
            }
          } 
        }
        
      }
      // 判断状态是否为滑动且滑动距离满足一定数值
      // 刷新（重新请求）初始化
      // 失败或完成后更改状态为false
      const touchEnd = () =>{
        if(tracking && isCanRefresh){
          this.refreshInfo = this.refreshInfoList[2]
          this.showImg = false;
          if(this.localStorageSkuList.length) {
            this.params = {
              items: this.localStorageSkuList
            }
          } else this.params = {}
          api.getCartList(this.params).then(({data,status,msg})=>{
            this.warehouseList = [];
            this.warehouseObj = {};
            this.shopName = data.shopList[0] ? this.cutString(data.shopList[0].shopName) : ""
            this.shopList = data.shopList.map((shops)=>{
              shops.checkAll = false;
              shops.itemsList.forEach(e=> {
                this.$set(e, "showBck", true) //图片loading
                this.$set(e, "showFailed", false)
                this.warehouseObj[e.warehouseName] ? this.warehouseObj[e.warehouseName].num++ :this.warehouseObj[e.warehouseName] = {
                  num: 1,
                  warehouse: e.warehouseName,
                  checkedNum: 0
                }
              })
              Object.keys(this.warehouseObj).forEach(key=> {
                this.warehouseList.push(this.warehouseObj[key])
              })
              return shops
            })
            this.checkedNum = 0;
            this.allShop = false;
            this.unvalidList = data.invalidList.map(e=> {
              this.$set(e, "showBck" , true)
              this.$set(e, "showFailed" , false)
              return e
            })
            this.itemsNum = data.itemsNum
            this.totalMoney = 0
            this.totalTax = 0
            this.checkedItemIdList = []
            this.checkedItemList = []

            scrollContent.style.transform = "translateY(0px)";
            scrollContent.style.transition = "transform 0.5s";
            this.refreshInfo = this.refreshInfoList[0];
            this.showImg = true;
            this.refreshDown = true

          }).catch(()=>{
            scrollContent.style.transform = "translateY(0px)";
            scrollContent.style.transition = "transform 0.5s";
            this.refreshInfo = this.refreshInfoList[0];
            this.showImg = true;
            this.refreshDown = true
          })
        } else if(tracking && isPullDown) {
          scrollContent.style.transform = "translateY(0px)";
          scrollContent.style.transition = "transform 0.5s";
          this.refreshInfo = this.refreshInfoList[0];
          this.showImg = true;
          this.refreshDown = true
        }
        tracking = false;
        isPullDown = false;
        isCanRefresh = false
      }
    },
    cutString(t) {
      return t && t.length > 7 ? t.slice(0, 7) + "..." : t
    },

    back() {
      utils.back();
    },

    checkItemType() {
      let checkedSkuNum = 0
      this.checkedItemList.forEach((item)=>{
        console.log(item)
        if(1 === item.saleType) {
          checkedSkuNum += item.skuNum
        }
      });
      console.log(checkedSkuNum<=10)
      return checkedSkuNum <= 10
    },

    goToPay() {
      const _that = this;

      return co(function* (val) {
        

        if(_that.checkedNum) {

          let orderList = [];
          _that.totalMoney =  Math.round(100 * _that.totalMoney) / 100;
          _that.checkedItemList.forEach((item)=>{
            item.orderId && orderList.push(item.orderId);
            item.price = Math.round(100 * item.price) / 100
          })
          if(_that.checkedNum > 50) {
            _that.toast("一次最多购买50种商品哟～");
            console.log("一次最多购买50种商");
            return 
          }
         if(!_that.checkItemType()) {
            _that.toast("每张订单只能买10个福袋哦~")
            console.log("每张订单只能买10个福袋哦");
          return
            
          }
          const params = {
            goodsTotalPrice: _that.totalMoney,
            goodsItem: _that.checkedItemList,
            syncCart: _that.localStorageSkuList
          }
          
           _that.h5Pay(params);

        } else{
                  _that.toast("请先选择要结算的商品");
        console.log("请先选择要结算的商品");
        }

      })()


    },
    h5Pay(params){
      const data = {
        items: this.checkedItemList,
        cartTotalMoneyAll: this.totalMoney,
        isCart: 1
      }
      params.buyerId = 0;
      try {
        localStorage.cartOrder =  JSON.stringify(params)
      }
      catch(e) {
        utils.clearLocalStorage();
        localStorage.cartOrder =  JSON.stringify(params)
      }
      if(this.ifLogin) {
        this.checkOrder()
      }
      else {
        try{
          localStorage.fromLogin = 1
        }
        catch(e) {
            utils.clearLocalStorage();
            localStorage.fromLogin = 1
        }
        userInterFace.goToLogin(encodeURIComponent(location.href))
        
      }
      return
    }
  }

  
})


