<template>
	<div class="board"   >
			<!-- <transition enter-active-class="animated faster fadeIn" leave-active-class="animated faster fadeOut" >
				<div class="mask" @click="confirm"  v-if="visible"></div>
			</transition> -->
			<transition name="mask" >
				<div class="mask" @click="confirm"  v-if="visible" @touchmove.prevent></div>
			</transition>
			<!-- <transition  enter-active-class="animated slideInRight faster" leave-active-class="animated faster slideOutRight" >
				<div class="main-filterBoard" v-if="visible">
					<div class="filterBoard-container">
						
					</div>
					<div class="bottom">
						<button class="reset">重置</button>
						<button class="confirm" @click="confirm">确定</button>
					</div>
				</div>
			</transition> -->
			<transition name="board" >
				<div class="main-filterBoard" v-if="visible">
					<div class="filterBoard-container">
						<div class="category-container">
							<h4>price</h4>
							<div class="category-price-container">
								<input
									type="tel" 
									:placeholder="isLowestPriceOnFocus ? '' : 'lowest'" 
									class="lowest"
									:class="{active: isLowestPriceOnFocus}"
									v-model="lowestPrice" 
									@focus="()=>{onFocus(0)}"
									@blur="()=>{onBlur(0)}"
									@keyup="()=>{onKeyup(0)}"
								>
								<span class="between">-</span>
								<input 
									type="tel" 
									:placeholder="isHighestPriceOnFocus ? '' : 'hightest'" 
									class="hightest"
									:class="{active: isHighestPriceOnFocus}"
									v-model="highestPrice" 
									@focus="()=>{onFocus(1)}"
									@blur="()=>{onBlur(1)}"
									@keyup="()=>{onKeyup(1)}"
								>
							</div>
						</div>
						<div class="category-container" v-for="(cate, cateIndex) in categoryList">
							<h4>{{cate.title}}</h4>
							<div class="show-selected" @click="()=>{showAll(cate.key)}">
								<span v-if="cate.filterList.length > 6 && !selectedNameInEachCategory[cate.key]">{{isShowAll.indexOf(cate.key) >-1 ? '收起': "全部"}}</span>
								<div v-if="selectedNameInEachCategory[cate.key]" class="selected-item">{{selectedNameInEachCategory[cate.key].join(",")}}</div>
								<i  v-if="cate.filterList.length > 6" class="anticon" 
									:class="{
										'icon-DescendFilled': isShowAll.indexOf(cate.key) === -1, 
										'icon-AscendFilled': isShowAll.indexOf(cate.key) > -1
										}"
								></i>
							</div>
							
							<div class="category-items-container" >
								<div class="item-line" v-for="(line, lineIndex) in initList(cate.filterList)">

									<div class="item word-ellipsis-1" v-for="(item, itemIndex) in line" v-if="lineIndex < 2 || isShowAll.indexOf(cate.key) > -1" :class="{'last-child':2 === line.length && 1 === itemIndex, active: selectedIdInEachCategory[cate.key] && selectedIdInEachCategory[cate.key].indexOf(item.id) > -1}" @click="()=>{active(item, cate.key)}">{{item.name}}</div>
								</div>
							</div>
						</div>
					</div>
					<div class="bottom">
						<button class="reset"  @click="reset">重置</button>
						<button class="confirm" @click="confirm">确定</button>
					</div>
				</div>
			</transition>

	</div>
</template>
<script>
import * as utils from "../../utils/utils"
export default {
	props: {
		visible: {
			default: false,
			type: Boolean
		},
		categoryList: {
			default: ()=>[],
			type: Array
		},
		listType: {
			default: "keyword",
			type: String,

		},
		listTypeId: {
			default: "",
			type: String
		},
		isFiltered: {
			default: false,
			type: Boolean
		}
	},
	data() {
		return {
			highestPrice: "",
			lowestPrice: "",
			isHighestPriceOnFocus: false,
			isLowestPriceOnFocus: false,
			selectedIdInEachCategory: {},
			selectedNameInEachCategory: {},
			isShowAll: [],
			isCanceled: false,
			prevFilter: {
				highestPrice: "",
				lowestPrice: "",
				selectedIdInEachCategory: {}
			}
		}

	},
	watch: {
		// 弹出筛选面板时设置body 位fixed隐藏滚动条, 设置overflow=hidden会无法touch??
		
		visible(t) {
			if(t) {
				var scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
				this.scrollTop = scrollTop;
				//document.body.style.overflow = "hidden"
			} else {
				//document.body.style.overflow = "auto"
			}
			// 根据url获取数据
			this.loadFilterStatus()
			
		}
	},
	
	mounted(){
		// 根据url获取数据
		this.loadFilterStatus();

	},
	methods: {

		reset() {
			this.highestPrice = "",
			this.lowestPrice = "",
			this.isHighestPriceOnFocus = false,
			this.isLowestPriceOnFocus = false,
			this.selectedIdInEachCategory = {},
			this.selectedNameInEachCategory = {},
			this.isShowAll = [],
			this.isCanceled = false;
		},
		showAll(cateIndex) {
			const currIndex = this.isShowAll.indexOf(cateIndex)
			if(currIndex > -1) {
				this.isShowAll.splice(currIndex, 1)
			} else {
				this.isShowAll.push(cateIndex)
			}
		},
		active(item, cateIndex) {

			const cate = this.selectedIdInEachCategory[cateIndex];
			const cateName = this.selectedNameInEachCategory[cateIndex];

			// 判断是否有结构
			if(!cate) {

				// 创建结构
				// 使用$set改变对象的值，和其指向相同地址的prev.selectedIdInEachCategory也会一同更改 ~~！@雾！！@~
				this.$set(this.selectedIdInEachCategory, cateIndex, []);
				this.$set(this.selectedNameInEachCategory, cateIndex, [])
				this.selectedIdInEachCategory[cateIndex].unshift(item.id)
				this.selectedNameInEachCategory[cateIndex].unshift(item.name)
				return void 0
			}
			const itIndex = cate.indexOf(item.id);
			// 判断是否已选择
			if(itIndex > -1 ) {
				// 已选择的取消选择
				cate.splice(itIndex, 1);
				cateName.splice(itIndex, 1);
				0 === cate.length && (this.$delete(this.selectedIdInEachCategory, cateIndex, []), this.$delete(this.selectedNameInEachCategory, cateIndex, []))
			} else {
				// 未选择的再最后插入
				cate.unshift(item.id)
				cateName.unshift(item.name)
			}
			
		},
		priceCorrectSort() {
			if(""!==this.highestPrice && "" !== this.lowestPrice && this.lowestPrice > this.highestPrice) {
				[this.highestPrice, this.lowestPrice ] = [this.lowestPrice, this.highestPrice ]
			}
		},
		getFilterList() {
			return {
				categories: this.selectedIdInEachCategory,
				categoriesName : this.selectedNameInEachCategory,
				price: {
					priceFlow: this.lowestPrice,
					priceCeil: this.highestPrice 
				}
			}
		},
		confirm() {
			// 先判断俩者是不是对象
			// 是对象后俩者长度是否一致
			// 判断俩个对象的所有key值是否相等相同
			// 判断俩个对象的相应的key对应的值是否相同
			// 来一个递归判断里面的对象循环1-4步骤
			// 
			// 数据结构
			// {
			// 		xxx: "",
			// 		xxx: "",
			// 		xxx: {
			// 			xxx: [],
			// 			xxx: []
			// 			
			// 		}
			// }

			let isDiff = false
			// 深度1
			
			for(let key in this.prevFilter) {

				if("selectedIdInEachCategory" === key) {

					// 是对象后俩者长度是否一致
					
					if(Object.keys(this.prevFilter[key]).length !== Object.keys(this[key]).length ){
						console.log(1)
						isDiff = true;
						break;
					}
					// 深度2
					for( let key2 in this.prevFilter[key]) {
						// 如果②中判断的更改了值跳出循环
						if(isDiff) {
							break
						}
						// ①判断俩个对象的所有key是否相等相同
						if(!(Object.keys(this[key]).indexOf(key2) > -1)) {
							console.log(2)
							isDiff = true;
							break;
						}
						
						const diff = () => {
							const curr = this[key][key2].sort();
							const prev = this.prevFilter[key][key2].sort();
							// 是对象后俩者长度是否一致
							if(curr.length !== prev.length) {
								console.log(3)
								isDiff = true;
								return "break"
							}

							// ②判断俩个对象的相应的key对应的值是否相同
							curr.some((it, itIndex)=> {
								if(it !== prev[itIndex]){
									console.log(4)
									isDiff = true;
									return true
								}
							})
						}
						if("break" === diff())
							break;

					}
				} else if(this.prevFilter[key] !== this[key]){
					console.log(5)
					isDiff = true;
					break;
				}
			}
			console.log(this.prevFilter.selectedIdInEachCategory[1])
			console.log(this.selectedIdInEachCategory[1])
			if(!isDiff){
				this.isCanceled = true
				this.$emit("update:visible", false)
				return void 0
			}
			// 这里是对象赋值-指向相同地址
			this.prevFilter.lowestPrice = this.lowestPrice;
            this.prevFilter.highestPrice = this.highestPrice;
            //this.prevFilter.selectedIdInEachCategory=JSON.parse(JSON.stringify(this.selectedIdInEachCategory));
            this.prevFilter.selectedIdInEachCategory = this.selectedIdInEachCategory;

			this.priceCorrectSort()
            const returnData = {
            	filters: JSON.stringify(this.selectedIdInEachCategory),
                priceFlow: this.lowestPrice,
                priceCeil: this.highestPrice
            }
            this.$emit("confirm", returnData)
            this.$emit("update:visible", false)
		},
		onFocus(t) {
			switch(t) {
				case 0: 
					this.isLowestPriceOnFocus = true;
					break;
				case 1:
					this.isHighestPriceOnFocus = true
			}
		},
		onBlur(t) {
			switch(t) {
				case 0: 
					this.isLowestPriceOnFocus = "" !== this.lowestPrice;
					break;
				case 1:
					this.isHighestPriceOnFocus = "" !== this.highestPrice
			}
		},
		onKeyup(t) {
			switch(t) {
				case 0: 
					if(this.lowestPrice.length > 9 ) {
						this.lowestPrice = this.lowestPrice.slice(0, 9)
					} else {
						this.lowestPrice = this.lowestPrice.replace(/[^0-9]+/, "")
					}
					break;
				case 1:
					if(this.highestPrice.length > 9 ) {
						this.highestPrice = this.highestPrice.slice(0, 9)
					} else {
						this.highestPrice = this.highestPrice.replace(/[^0-9]+/, "")
					}
			}
			
		},
		stopEvent() {
			return false
		},
		loadFilterStatus() {
			const detailFilter = utils.getQueryString("detailFilter");
			if(detailFilter) {
				const obj = JSON.parse(decodeURIComponent(detailFilter));
				if(0 === Object.keys(obj).length){
					this.$emit("update:isFiltered", false)
				} else {
					// 直接赋值， 一同指向相同地址的prev.selectedIdInEachCategory  不会发生改变 ~！@雾#@！~
					this.highestPrice = obj.price.priceCeil;
					this.lowestPrice = obj.price.priceFlow;
					this.selectedNameInEachCategory = obj.categoriesName;
					this.selectedIdInEachCategory = obj.categories;
					if(this.highestPrice || this.lowestPrice || 0 !== Object.keys(this.selectedNameInEachCategory).length || 0 !== Object.keys(this.selectedIdInEachCategory).length) {
						this.isLowestPriceOnFocus = this.lowestPrice !== "";
						this.isHighestPriceOnFocus = this.highestPrice !== "";
						this.$emit("update:isFiltered", true)
					} else {
						this.isLowestPriceOnFocus = false;
						this.isHighestPriceOnFocus = false;
						this.$emit("update:isFiltered", false)
					}
				}
			} else {
				this.$emit("update:isFiltered", false)
			}
		},
		initList(t) {
			let line = [];
			for(let i=0;i<t.length;i++){
				if(0 === i%3) {
					line.push([])
				}
				line[line.length - 1].push(t[i])
			}
			
			return line

		}
	}
}
</script>
<style lang=scss>
