}*/

.board{
    .mask {
    	position: fixed;
	    z-index: 1001;
	    left: 0;
	    top: 0;
	    width: 100%;
	    height: 100%;
	    background-color: #000;
	    opacity: 0.5;
	    /*background: rgba(0, 0, 0, 0.5);*/
    }
	.main-filterBoard {
		width: 90%;
		height: 100%;
		background-color: #FFF;
		z-index: 1002;
		position: fixed;
		top: 0;
		left: 10%;
		bottom: 0;
		right: 0;
		overflow: auto;
		display: flex;
		box-orient: vertical;
		flex-direction: column;
		transition: left .4s;
		.filterBoard-container{
			flex: 1;
			overflow: auto;
    		padding-bottom: 30px;

		}
		.category-container{
			position: relative;
    		margin: 20px 10px 0;
    		h4 {
    			font-size: 14px;
    			margin-bottom: 14px;
    			line-height: 14px;
    		}
    		.show-selected {
    		    position: absolute;
			    font-size: 12px;
			    right: 0;
			    top: 0;
			    width: 70%;
			    text-align: right;
			    display: flex;
    			justify-content: flex-end;
    		}
    		.selected-item {
			    overflow: hidden;
			    width: 90%;
			    white-space: nowrap;
			    text-overflow: ellipsis;
			    color: #4cc3ff;
    		}
			.category-price-container{
				display: flex;
				input {
					flex: 1;
					background-color: #FFF;
					
					display: block;
					font-size: 12px;
					letter-spacing: 1px;
					text-align: center;
					background-color: #f4f4f4;
					
					height: 30px;
					&.active {
						border: 1px #4cc3ff solid;
						color: #4cc3ff;
						background-color: #FFF;
					}
				}

				.between {
					height: 30px;
					vertical-align: middle;
					line-height: 30px;
					width: 20px;
					text-align: center;
				}
			}
			.category-items-container{
				.item-line{
					display: flex;
					justify-content: space-between;
					.item{
						width: 31.33%;
					    height: 30px;
					    margin-bottom: 10px;
					    border-radius: 2px;
					    background: #f4f4f4;
					    text-align: center;
					    line-height: 30px;
					    padding: 0 5px;
					    font-size: 12px;
					    &.active {
					    	border: 1px #4cc3ff solid;
							color: #4cc3ff;
							background-color: #FFF;
							line-height: 28px;
					    }
					    &.last-child {
					    	/*-(31.33% * 3 - 100%) / 2 + 31.33*/
					    	margin-right: 34.33%
					    }
					}
				}
			}
		}
		.bottom{
			padding: 8px 16px;
			border-top: 1px solid #ebeff0;
			position: relative;
			display: flex;
			justify-content: space-between;
			.confirm, .reset{
				font-size: 14px;
				border: 1px #4cc3ff solid;
				height: 40px;
				line-height: 40px;
				width: 48%;
				color: #4cc3ff;
				text-align: center;
				background-color: #FFF;
			}
			.confirm {
				background-color: #4cc3ff;
				color: #FFF
			}

		}
	}
	.mask-enter-active, .mask-leave-active {
	  transition: opacity .5s;
	}
	.mask-enter, .mask-leave-to {
	  opacity: 0;
	}
	.board-enter-active, .board-leave-active {
	  transition: left .4s;
	}
	.board-enter, .board-leave-to  {
	  left: 100%;
	}
}

</style>



