<template>
	<div class="toast">
		<div class="toast-content">
			{{info}}
		</div>
	</div>
</template>
<script>
export default{
	props:{
		duration: {
			type: Number,
			default: 3e3
		},
		info: {
			type: String,
			default: ""
		}
	},
}
</script>
<style lang="scss">
.toast {
	position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    .toast-content {
    	position: absolute;
	    top: 50%;
	    left: 50%;
	    transform: translateX(-50%) translateY(-50%);
	    padding: 15px 20px;
	    background-color: rgba(0,0,0,.65);
	    line-height: 20px;
	    max-width: 75%;
	    border-radius: 8px;
	    color: #fff;
	    font-size: 14px;
	    text-align: center;
    }
}
</style>


