let isBack = false;

export function getQueryString (name, isReturnNull = false) {
    const reg = new RegExp("(^|&|#)" + name + "=([^&]*)(&|$)", "i");
    const n = location.hash.match(reg) || window.location.search.slice(1).match(reg);
    if(null == n){
    	return isReturnNull ? null : ""
    } else {
    	return decodeURI(n[2])
    }
}
export function getQueryWord (name, isReturnNull = false) {
    const reg = new RegExp("(^|&|#)" + name + "=([^&]*)(&|$)", "i");
    const n = location.hash.match(reg) || window.location.search.slice(1).match(reg);
    if(null == n){
    	return isReturnNull ? null : ""
    } else {
    	return decodeURIComponent(n[2])
    }
}

export function replaceQueryParam(name, value, n = false, replaceState = true) {
    let url;
    // 先判断url上是否有参数
    if(null !== getQueryString(name, true)) {
        const reg = new RegExp(name + "=([^&]*)");
        if(n) {
            url = window.location.href.replace(new RegExp("&" + name + "=([^&]*)"), "")
            url = url.replace(reg, "")
        } else {
            url = window.location.href.replace(reg, `${name}=${encodeURI(value)}`)
        }
    } else {
        if(!n){
            if(window.location.href.indexOf("#") > -1 ) {
                url = window.location.href + "&" + name + "=" + encodeURI(value)
            } else {
                url = window.location.href + "#" + name + "=" + encodeURI(value);
            }
        } 
        
    }
    replaceState && history.replaceState(null, "", url)
    return url

}


export function back() {
    isBack = true;
    window.history.go(-1);
    return void 0
}

/*清处浏览留下的记录*/
export function clearLocalStorage() {
    for(let l in localStorage) {
        try{
            if(parseInt(l, 10)){
                localStorage.removeItem(l)
            }
        }
        catch(e) {}
    }
}

export function handleStorage(callback = function() {}) {
    try {
        localStorage.NeedFrefresh = 0
    } catch (e) {
        localStorage.NeedFrefresh = 0
    }
    // 当同源页面的某个页面修改了localStorage,其余的同源页面只要注册了storage事件，就会触发
    window.addEventListener("storage", (storageEvent) => {
        !function(storageEvent, callback) {
            if("needRefresh" === storageEvent.key) {
                if(isBack) return false;
                // 0是默认值。 如果监测到有页面更改变成1时触发回调
                1 === new Number(event.newValue) && (localStorage.needRefresh = 0, callback())
            }
        }(storageEvent, callback)
    })
}


